___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
}

.top-logo {
   position: relative;
  display: flex;
  justify-content: center;
  margin-top: 0px;
}

.top-logo img {
  height: 250px;
  width: auto;
}

main {
  max-width: 900px;
  margin: 0px auto 60px auto;
  background-color: #ffffff;
  padding: 24px;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  text-align: center;
}

/* TITOLO */
main .title {
  text-align: center;
  margin: 0 0 10px 0;
}

/* SOTTOTITOLO */
.subtitle {
  text-align: center;
  margin: 0 0 20px 0;
  color: #444;
}

/* FORM */
form {
  display: flex;
  gap: 10px;
  margin-bottom: 18px;
  justify-content: center;
}

input[type="text"] {
  flex: 1;
  padding: 12px;
  border-radius: 12px;
  border: 1px solid #ddd;
  outline: none;
  text-align: left;
}

input[type="text"]:focus {
  border-color: #bbb;
}

/* BOTTONI */
button {
  padding: 12px 16px;
  border-radius: 12px;
  border: 1px solid #ddd;
  background: #ffffff;
  cursor: pointer;
  transition: transform 0.12s ease, box-shadow 0.12s ease;
}

button:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.08);
}

button:active {
  transform: translateY(1px) scale(0.98);
  box-shadow: none;
}

/* MESSAGGI */
#status {
  color: #333;
  margin: 10px 0;
  text-align: center;
}

#error {
  color: #c62828;
  margin: 10px 0;
  text-align: center;
}

/* RISULTATI */
#results {
  margin-top: 10px;
  border-radius: 14px;
  overflow: hidden;
  text-align: left;
}

#results > div {
  padding: 12px;
  border-bottom: 1px solid #eee;
  background: #fff;
  text-align: left;
}

#results > div:last-child {
  border-bottom: none;
}

/* DETTAGLI */
#details {
  margin-top: 18px;
  padding: 16px;
  background-color: #f3f3f3;
  border-radius: 14px;
}

/* INPUT ERRORE */
input.error {
  border-color: #e53935;
  animation: shake 0.3s;
}

/* ANIMAZIONE */
@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
  100% { transform: translateX(0); }
}

.details-btn {
  margin-top: 8px;
}

.book-card {
  position: relative;
}

.inline-details {
  margin-top: 12px;
  background: #f3f3f3;
  border: 1px solid #e6e6e6;
  border-radius: 12px;
  padding: 14px;
}

.inline-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.close-details {
  border: none;
  background: transparent;
  font-size: 22px;
  line-height: 1;
  cursor: pointer;
  padding: 2px 8px;
}

.inline-title {
  margin: 0 0 8px 0;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.spinner {
  width: 22px;
  height: 22px;
  border: 3px solid #ddd;
  border-top-color: #555;
  border-radius: 50%;
  margin: 8px auto;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.inline-loading {
  display: flex;
  align-items: center;
  gap: 10px;
}


